.pl {
  .visually-hidden {
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px);
    overflow: hidden;
    height: 1px;
    width: 1px;
    word-wrap: normal;
  }

  .sg-pattern {
    clear: left;
  }

  #icons {
    display: flex;

    & > div {
      border: 1px solid #eee;
      padding: 1em;
      text-align: center;
    }
  }

  [role=main] {
    overflow: visible;
  }

  .site-header .navigation {
      background: $navy;
  }
}
